@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif;
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  background-size: cover;
  background-image: 
  linear-gradient(0deg, rgba(0, 0, 0, 6.5e-05) 0%, rgba(0, 0, 0, 0.65) 100%),
  url(/CIJS75_Final_Project/static/media/background.4df06a7a.jpeg);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #333; 
}

::-webkit-scrollbar-thumb:hover {
  background: #000; 
}

#header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 3%;
    position: fixed;
    width: 100%;
    transition: 0.4s;
    z-index: 2;
    height: 80px;
}

.user-btn-avatar-container {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border: solid 1px #7f8c8d;
}

.user-btn-avatar {
    min-width: 100%;
    aspect-ratio: 1/1;
    object-fit: cover;
}

.logo {
    height: 60px;
    width: auto;
    object-fit: cover;
    transition: 0.4s;
}

.logo:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.header-nav {
    display: flex;
    align-items: center;
    color: #fff;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    height: 100%;
}

.header-nav>a {
    color: #ffedbb;
    text-transform: uppercase;
    font-weight: 800;
    font-size: 20px;
    margin-right: 4vw;
    transition: 0.4s;
}

.header-nav>a:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

@-webkit-keyframes appear {
    from {-webkit-transform: translateY(-60px);transform: translateY(-60px);opacity: 0;}
    to {-webkit-transform: translateY(0);transform: translateY(0);opacity: 1;}
}

@keyframes appear {
    from {-webkit-transform: translateY(-60px);transform: translateY(-60px);opacity: 0;}
    to {-webkit-transform: translateY(0);transform: translateY(0);opacity: 1;}
}

.login-signup-btn {
    font-size: 16px;
    display: none;
    grid-gap: 16px;
    gap: 16px;
    align-items: center;
    font-size: 16px;
}

.login-signup-btn.active {
    display: flex;
}

.login-signup-btn>a {
    color: #fff;
    background-color: #e18936;
    padding: 8px 16px;
    display: flex;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
    transition: 0.4s;
}

.login-signup-btn>a:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.user-menu {
    background-color: #e18936;
    padding: 8px 16px;
    display: none;
    align-items: center;
    justify-content: center;
    position: relative;
    box-sizing: content-box;
    cursor: pointer;
    transition: 0.4s;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    -webkit-animation: appear 0.5s;
            animation: appear 0.5s;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    white-space: nowrap;
    grid-gap: 5px;
    gap: 5px;
}

.user-menu.active {
    display: flex;
}

.user-menu:hover {
    background-color: #a36124;
}

.user-menu:hover .user-submenu {
    opacity: 1;
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
}

.user-submenu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 100%;
    top: 100%;
    cursor: pointer;
    opacity: 0;
    transition: 0.5s;
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
    -webkit-transform-origin: top;
            transform-origin: top;
}

.user-submenu>* {
    background-color: #e18936;
    width: 100%;
    padding: 8px 16px;
    border: none;
    cursor: pointer;
    color: #fff;
    border-top: solid 1px #34495e;
    transition: 0.4s;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    grid-gap: 5px;
    gap: 5px;
}

.user-submenu>*:hover {
    background-color: #a36124;
}


.burger-btn {
    display: none;
    align-items: center;
    padding: 6px;
    background: none;
    border: none;
    color: #fff;
    cursor: pointer;
    transition: 0.4s;
    border-radius: 50%;
}

.burger-btn:hover {
    color: #e18936;
}

.burger-btn.active {
    color: #e18936;
    background-color: #fff;
}

#burger-menu-container {
    position: fixed;
    width: 100%;
    height: calc(100vh - 80px);
    top: 80px;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.2);
    display: none;
    justify-content: end;
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;
}

#burger-menu-container.active {
    opacity: 1;
    visibility: visible;
}

.burger-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #fff;
    border: solid 1px #e18936;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    visibility: hidden;
    -webkit-transform-origin: right;
            transform-origin: right;
    transition: 0.5s;
}

.burger-menu.active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    visibility: visible;
}

.burger-menu>* {
    width: 100%;
    text-align: center;
    transition: 0.4s;
    font-size: 20px;
}

.burger-signin {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.burger-signin>* {
    width: 100%;
    font-size: 20px;
    transition: 0.4s;
    text-align: center;
}

.burger-menu strong {
    font-size: 20px;
}

.burger-login, .burger-signup, .burger-logout {
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 5px;
    gap: 5px;
    padding: 10px 0;
}

.burger-login {
    color: #2980b9;
}

.burger-login:hover {
    color: #fff;
    background-color: #2980b9;
}

.burger-signup {
    color: #27ae60;
}

.burger-signup:hover {
    color: #fff;
    background-color: #27ae60;
}

.burger-logout {
    cursor: pointer;
    color: #e74c3c;
    border: none;
    background: none;
    display: none;
}

.burger-logout.active {
    display: flex;
}

.burger-logout:hover {
    color: #fff;
    background-color: #e74c3c;
}

.burger-link {
    color: #e18936;
    font-weight: bold;
    padding: 10px 0;
}

.burger-link:hover {
    color: #fff;
    background-color: #e18936;
}

.burger-user {
    white-space: nowrap;
    color: #fff;
    background-color: #e18936;
    display: flex;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
    justify-content: center;
    padding: 10px;
    font-size: 20px;
}

.back-to-top {
    color: aliceblue;
    background-color: #e18936;
    padding: 16px;
    font-size: 25px;
    text-decoration: none;
    transition: 0.4s;
    position: fixed;
    bottom: 50px;
    right: 50px;
    border: none;
    cursor: pointer;
    z-index: 2;
    border: 1px solid #000;
}

.back-to-top:hover {
    background-color: #a36124;
}

.back-to-top:active {
    background-color: #e18936;
}

@media only screen and (max-width: 992px) {
    .burger-btn {
        display: flex;
    }
    #header {
        background-color: #2c3e50;
    }
    .header-nav>a, .user-menu.active, .login-signup-btn.active {
        display: none;
    }
    #burger-menu-container {
        display: flex;
    }
}

@media only screen and (max-width: 600px) {
    .burger-menu {
        width: 90vw;
    }
}
.login {
    min-height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16vh 0;
}

@-webkit-keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translateY(-400px);
                transform: translateY(-400px);
    }

    ;

    to {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translateY(-400px);
                transform: translateY(-400px);
    }

    ;

    to {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

.login-form {
    width: 420px;
    max-width: 30%;
    min-width: 400px;
    background-color: #ecf0f1;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    -webkit-animation: fadein 0.5s;
            animation: fadein 0.5s;
}

.login-form>h1 {
    padding: 16px;
    font-weight: bold;
    font-size: 26px;
    margin-bottom: 10px;
}

.login-form>input {
    width: 85%;
    padding: 10px;
    outline: none;
    font-size: 16px;
    transition: 0.4s;
    margin-bottom: 20px;
}

.login-form>button {
    width: 85%;
    padding: 10px;
    background-color: #16a085;
    color: #fff;
    border: none;
    cursor: pointer;
    transition: 0.4s;
    margin-bottom: 20px;
}

.login-form>a {
    width: 85%;
    padding: 10px;
    background-color: #e74c3c;
    color: #fff;
    border: none;
    cursor: pointer;
    transition: 0.4s;
    margin-bottom: 30px;
    text-align: center;
}

.login-form>button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.login-form>a:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.login-with {
    display: flex;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
    margin-bottom: 20px;
}

.login-form img {
    width: 46px;
    height: auto;
    cursor: pointer;
    transition: 0.4s;
}

.login-form img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.login-form>p {
    margin-bottom: 20px;
    width: 85%;
    text-align: center;
}

.login-form span {
    color: #3498db;
    font-weight: bold;
    cursor: pointer;
}

.login-form span:hover {
    text-decoration: underline;
}

.login-form>p>a {
    color: #3498db;
    font-weight: bold;
    cursor: pointer;
}

.login-form>p>a:hover {
    text-decoration: underline;
}

.user {
    margin-bottom: 20px;
}

.pw-forgot {
    margin-bottom: 20px;
    padding-left: 5px;
    width: 85%;
}

.pw-forgot>a {
    cursor: pointer;
    color: #3498db;
    font-weight: bold;
}

.pw-forgot>a:hover {
    text-decoration: underline;
}

.user.active {
    background-color: #ff7d6e;
}

.error {
    opacity: 0;
    margin-bottom: 20px;
    transition: 0.5s;
    color: red;
    width: 85%;
    text-align: center;
}

.error.active {
    opacity: 1;
    -webkit-animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
            animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

@-webkit-keyframes shake {
    10%,
    90% {
        -webkit-transform: translate3d(-1px, 0, 0);
                transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        -webkit-transform: translate3d(2px, 0, 0);
                transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        -webkit-transform: translate3d(-4px, 0, 0);
                transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        -webkit-transform: translate3d(4px, 0, 0);
                transform: translate3d(4px, 0, 0);
    }
}

@keyframes shake {
    10%,
    90% {
        -webkit-transform: translate3d(-1px, 0, 0);
                transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        -webkit-transform: translate3d(2px, 0, 0);
                transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        -webkit-transform: translate3d(-4px, 0, 0);
                transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        -webkit-transform: translate3d(4px, 0, 0);
                transform: translate3d(4px, 0, 0);
    }
}

@media only screen and (max-width: 768px) {
    .login-form>h1 {
        padding: 12px;
    }
    .login-form>input {
        margin-bottom: 12px;
    }
    .pw-forgot {
        margin-bottom: 12px;
    }
    .error {
        margin-bottom: 12px;
    }
    .login-form>button {
        margin-bottom: 12px;
    }
    .login-form>p {
        margin-bottom: 12px;
    }
    .login-with {
        margin-bottom: 12px;
    }
}

@media only screen and (max-width: 600px) {
    .login-form {
        min-width: 90%;
    }
    .login-form img {
        width: 36px;
    }
}
