#mytodolist {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16vh 0;
    min-height: 100vh;
}

@-webkit-keyframes todolist {
    from {opacity: 0; -webkit-transform: translateX(200px); transform: translateX(200px);};
    to {opacity: 1; -webkit-transform: translateX(0); transform: translateX(0);};
}

@keyframes todolist {
    from {opacity: 0; -webkit-transform: translateX(200px); transform: translateX(200px);};
    to {opacity: 1; -webkit-transform: translateX(0); transform: translateX(0);};
}

.todolist-container {
    width: 50%;
    min-width: 700px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    background-color: #fff;
    border: 1px solid #e18936;
    -webkit-animation: todolist 0.5s ease-in-out;
            animation: todolist 0.5s ease-in-out;
}

.save-undo {
    position: fixed;
    left: 50%;
    bottom: 10%;
    padding: 10px;
    background-color: #fff;
    border: solid 2px #34495e;
    z-index: 1;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    transition: 0.5s;
    opacity: 0;
}

.save-undo.active {
    opacity: 1;
}

.save-todolist {
    top: 20px;
    right: 20px;
    background-color: #fff;
    color: #16a085;
    border: 2px solid #16a085;
    padding: 10px;
    cursor: pointer;
    transition: 0.4s;
    font-weight: bold;
}

.save-todolist:hover {
    background-color: #16a085;
    color: #fff;
}

.undo-todolist {
    top: 20px;
    right: 20px;
    background-color: #fff;
    color: #2980b9;
